          {'briefImg': require("../../assets/images/022.png"),'briefTitle':'IT外包服务','briefText': 'IT咨询规划-产品设计-开发-测试-调试-验收-上线-售后维护','briefAlt': ''},
          {'briefImg': require("../../assets/images/023.png"),'briefTitle':'网络安全','briefText': '对网络流量和终端日志，精准发现网络中的入侵行为，并结合威胁和攻击链分析进行快速响应处置。针对网络威胁发现、分析、响应、处置、溯源等的一体化解决方案，帮助用户快速感知和应对复杂网络威胁环境','briefAlt': ''},
