
<template>
  <div class="platform_advantage">
    <div class="rowFluid">
      <div class="span12">
        <div class="container">
          <div class="all_title2 wow fadeInUp">
            <h3 class="title">产品与服务</h3>
            <div class="text">PRODUCTS SERVICES</div>
          </div>
          <div class="rowFluid">
            <div class="platform_advantage_content">
              <div class="span4 col-xm-6 col-xs-12 wow bounceInLeft">
                <div class="platform_advantage_list">
                  <div class="platform_advantage_img">
                    <img :src="picData.advanceList[0]" >
                  </div>
                  <div class="platform_advantage_brief">
                    <div class="brief_title">ERP实施</div>
                    <div class="brief_text">用友U8+ 用友T3  <br> 思爱普SAP abasERP</div>
                  </div>
                </div>
              </div>
              <div class="span4 col-xm-6 col-xs-12 wow bounceIn">
                <div class="platform_advantage_list">
                  <div class="platform_advantage_img">
                    <img :src="picData.advanceList[1]" >
                  </div>
                  <div class="platform_advantage_brief">
                    <div class="brief_title">IT外包服务</div>
                    <div class="brief_text">
                      开发→测试→上线 app开发
                      <br> 小程序开发 网站开发
                    </div>
                  </div>
                </div>
              </div>
              <div class="span4 col-xm-6 col-xs-12 wow bounceInRight">
                <div class="platform_advantage_list">
                  <div class="platform_advantage_img">
                    <img :src="picData.advanceList[2]" alt="响应式自助建站">
                  </div>
                  <div class="platform_advantage_brief">
                    <div class="brief_title">WMS仓储系统</div>
                    <div class="brief_text">
                      2D/3D定位系统 高精度标签
                      <br>定位系统
                    </div>
                  </div>
                </div>
              </div>
              <div class="span4 col-xm-6 col-xs-12 wow bounceInLeft">
                <div class="platform_advantage_list">
                  <div class="platform_advantage_img">
                    <img :src="picData.advanceList[3]">
                  </div>
                  <div class="platform_advantage_brief">
                      <div class="brief_title">数据采集与分析</div>
                      <div class="brief_text">
                        可视化数据分析 声、光、温度、
                        <br>化学、电机等传感器 RFID 
                        <br>数据记录仪
                      </div>
                  </div>
                </div>
              </div>
              <div class="span4 col-xm-6 col-xs-12 wow bounceIn">
                <div class="platform_advantage_list">
                  <div class="platform_advantage_img">
                    <img :src="picData.advanceList[4]" alt="html5建站平台">
                  </div>
                  <div class="platform_advantage_brief">
                    <div class="brief_title">网络系统工程</div>
                    <div class="brief_text">
                       数据中心系统 堡垒机 负载均衡 <br> 路由器 服务器 防火墙 审计系统 
                    </div>
                  </div>
                </div>
              </div>
              <div class="span4 col-xm-6 col-xs-12 wow bounceInRight">
                <div class="platform_advantage_list">
                  <div class="platform_advantage_img">
                    <img :src="picData.advanceList[5]" alt="H5响应式网站建设">
                  </div>
                  <div class="platform_advantage_brief">
                    <div class="brief_title">网络安全</div>
                    <div class="brief_text">
                      审计系统 日志维护 服务器运维 
                      <br>数据库维护
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div class="rowFluid">
          <div class="waves_box">
            <canvas id="waves" class="waves"></canvas>
          </div>
        </div>
        <div class="container">
          <div class="rowFluid">
            <div class="response_shows">
              <div class="response_shows_box wow bounceIn">
                <div class="response_shows_width">
                  <div class="pc">
                    <img :src="picData.showList[0]" >
                    <div class="pic">
                      <div class="item">
                        <img :src="picData.detailList[0]" >
                      </div>
                      <div class="item">
                        <img :src="picData.detailList[1]">
                      </div>
                      <div class="item">
                        <img :src="picData.detailList[2]" >
                      </div>
                    </div>
                  </div>
                  <div class="pad">
                    <img :src="picData.showList[2]" >
                    <div class="pic">
                      <div class="item">
                        <img :src="picData.detailList[3]" >
                      </div>
                      <div class="item">
                        <img :src="picData.detailList[4]" >
                      </div>
                      <div class="item">
                        <img :src="picData.detailList[5]" >
                      </div>
                    </div>
                  </div>
                  <div class="phone">
                    <img :src="picData.showList[1]" >
                    <div class="pic">
                      <div class="item">
                        <img :src="picData.detailList[6]" >
                      </div>
                      <div class="item">
                        <img :src="picData.detailList[7]" >
                      </div>
                      <div class="item">
                        <img :src="picData.detailList[8]" >
                      </div>
                    </div>
                  </div>
                  <div class="thumb"></div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      picData: {
        advanceList: [
          require("../../assets/images/008.png"),
          require("../../assets/images/009.png"),
          require("../../assets/images/010.png"),
          require("../../assets/images/011.png"),
          require("../../assets/images/012.png"),
          require("../../assets/images/013.png")
        ],
        showList: [
          require("../../assets/images/015.png"),
          require("../../assets/images/016.png"),
          require("../../assets/images/017.png")
        ],
        detailList: [
          require("../../assets/images/11.jpg"),
          require("../../assets/images/11-1.jpg"),
          require("../../assets/images/11-2.jpg"),
          require("../../assets/images/12.jpg"),
          require("../../assets/images/12-1.jpg"),
          require("../../assets/images/12-2.jpg"),
          require("../../assets/images/13.jpg"),
          require("../../assets/images/13-1.jpg"),
          require("../../assets/images/13-2.jpg")
        ]
      }
    };
  },
  mounted() {
    this.gundong();
  },
  methods: {
    gundong() {
      if ($(".response_shows_box .item").length > 1) {
        var _caseTime = 4000,
          _casePc = $(".response_shows_box .pc .pic .item"),
          _casePad = $(".response_shows_box .pad .pic .item"),
          _casePhone = $(".response_shows_box .phone .pic .item"),
          _caseLength = _casePc.length,
          _casePage = -1,
          _casePlay;
        _casePc.each(function(index, element) {
          $(".response_shows_box .thumb").append("<span></span>");
        });
        var _caseSpan = $(".response_shows_box .thumb span");
        _caseSpan.mouseover(function() {
          _casePage = $(this).index() - 1;
          caseLoop();
        });

        function caseLoop() {
          clearInterval(_casePlay);
          _casePage++;
          _casePage = _casePage < _caseLength && _casePage > 0 ? _casePage : 0;
          var _pcH = -_casePc.height() * _casePage + "px",
            _padH = -_casePad.height() * _casePage + "px",
            _phoneH = -_casePhone.height() * _casePage + "px";
          _caseSpan
            .removeClass("active")
            .eq(_casePage)
            .addClass("active");
          _casePc.css({
            transform: "translateY(" + _pcH + ")",
            "-ms-transform": "translateY(" + _pcH + ")",
            "-moz-transform": "translateY(" + _pcH + ")",
            "-webkit-transform": "translateY(" + _pcH + ")",
            "-o-transform": "translateY(" + _pcH + ")"
          });
          _casePad.css({
            transform: "translateY(" + _padH + ")",
            "-ms-transform": "translateY(" + _padH + ")",
            "-moz-transform": "translateY(" + _padH + ")",
            "-webkit-transform": "translateY(" + _padH + ")",
            "-o-transform": "translateY(" + _padH + ")"
          });
          _casePhone.css({
            transform: "translateY(" + _phoneH + ")",
            "-ms-transform": "translateY(" + _phoneH + ")",
            "-moz-transform": "translateY(" + _phoneH + ")",
            "-webkit-transform": "translateY(" + _phoneH + ")",
            "-o-transform": "translateY(" + _phoneH + ")"
          });
          _casePlay = setTimeout("caseLoop()", _caseTime);
        }
        // caseLoop()
      }
    }
  }
};
</script>
<style lang="less" rel="stylesheet/less" scoped>
</style>


